<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">Badge 标记</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                在右上角展示徽标数字或小红点。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-code-desc">设置 content 属性后，Badge 会在子元素的右上角显示对应的徽标，也可以通过 dot 来显示小红点。</div>
            <div class="hb-a">
                <div style="float:left;margin-right: 15px;">
                    <Badge :content="4">
                        <div class="child-test"><img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /></div>
                    </Badge>
                </div>
                <div style="float:left;margin-right: 15px;">
                    <Badge :content="4" :dot="true">
                        <div class="child-test"><img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /></div>
                    </Badge>
                </div>

            </div>
            <div class="hb-code">
                <div class="hb-code-desc">通过content来设置显示数字，通过dot来显示小红点。</div>
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                        &lt;Badge :content="4"&gt;
                            &lt;div class="child-test"&gt;&lt;img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /&gt;&lt;/div&gt;
                        &lt;/Badge>
                        &lt;Badge :content="4" :dot="true"&gt;
                            &lt;div class="child-test"&gt;&lt;img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /&gt;&lt;/div&gt;
                        &lt;/Badge>
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">最大值</div>
            <div class="hb-code-desc">设置 max 属性后，当 content 的数值超过最大值时，会自动显示为 {max}+。</div>
            <div class="hb-a">
                <Badge :content="44" :max="'12'">
                    <div class="child-test"><img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /></div>
                </Badge>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                    &lt;Badge :content="44" :max="12"&gt;
                        &lt;div class="child-test"&gt;&lt;img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /&gt;&lt;/div&gt;
                    &lt;/Badge&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">自定义颜色</div>
            <div class="hb-code-desc">通过 color 属性来设置徽标的颜色。</div>
            <div class="hb-a">
                <div style="float:left;margin-right: 15px;">
                    <Badge :content="88" :color="'blue'">
                        <div class="child-test"><img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /></div>
                    </Badge>
                </div>
                <div style="float:left;margin-right: 15px;">
                    <Badge :content="66" :color="'#000000'">
                        <div class="child-test"><img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /></div>
                    </Badge>
                </div>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                        &lt;Badge :content="88" :color="'blue'"&gt;
                            &lt;div class="child-test"&gt;&lt;img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /&gt;&lt;/div&gt;
                        &lt;/Badge&gt;

                        &lt;Badge :content="66" :color="'#000000'"&gt;
                            &lt;div class="child-test"&gt;&lt;img src="../../../static/images/war-wolf.jpg" width="100%" height="100%" /&gt;&lt;/div&gt;
                        &lt;/Badge&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
    import Badge from '@/components/template/Badge'
    export default {
        name: 'wlBadge',
        data() {
            return {
                tableData: [
                    {
                        param: 'content',
                        desc: '徽标内容',
                        type: 'number | string',
                        defaultVal: '',
                    },
                    {
                        param: 'dot',
                        desc: '是否展示为小红点',
                        type: 'boolean',
                        defaultVal: 'false',
                    },
                    {
                        param: 'max',
                        desc: '最大值，超过最大值会显示 {max}+，仅当 content 为数字时有效',
                        type: 'number | string',
                        defaultVal: '',
                    },
                    {
                        param: 'color',
                        desc: '徽标背景颜色',
                        type: 'string',
                        defaultVal: '',
                    },

                ]
            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            Badge
        }
    }
</script>
<style scoped>
    .child-test{width: 50px;height: 50px;border:1px solid #ccc;}
    .hb-a{padding-top:20px;}
</style>



