<template>
    <div class="badge">
        <div class="badge-content">
            <slot></slot>
            <div class="badge-tips" :class="{dots:dot}" :style="{background:color}">
                <span v-show="!dot">{{getContent}}</span>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'badge',
        props:{
            content: [String,Number],
            color:{
                type:String,
                default:null
            },
            dot:{
                type:Boolean,
                default:false,
            },
            max:[String,Number]
        },
        computed:{
            getContent(){
                let ctt = 0;
                if(this.max){
                    if(parseFloat(this.content) > parseFloat(this.max)){
                        ctt = this.max + '+';
                    }else{
                        ctt = this.content;
                    }
                }else{
                    ctt = this.content;
                }
                return ctt;
            }
        },
        filters:{

        },
        data() {
            return {
            }
        },
        mounted(){

        },
        methods:{

        }
    }
</script>
<style scoped>
    .badge{height: auto;}
    .badge-content{float:left;position: relative;}
    .badge-tips{position: absolute;width: auto;height: 14px;padding-left:2px;padding-right: 2px;background: red;line-height: 14px;
        top:-7px;right:-6px;color:#fff;font-size: 12px;border-radius: 7px;min-width: 10px;text-align: center;
    }
    .badge-tips.dots{width: 8px;height: 8px;border-radius: 50%;padding:0px;top:-3px;right:-3px;min-width: inherit;}
</style>


